<template>
  <div>
    <el-dialog  :visible.sync="visible" width="600px"  :show-close="false">
      <img class=".erweima" src="../assets/erweima.jpg">
    </el-dialog>
    <div class=".bg">
      <a @click="visible=true">
        <el-row >
          <el-col :span="8" v-for="i in list" :key="i">
            <img class=".img" src="../assets/c1.png">
            <p>关注进入</p>
          </el-col>
        </el-row>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      visible: false,
      list: [],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  created () {
    for (let i = 0; i < 15; i++) {
      this.list.push(i)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bg {
  width: 100%;
  height: 800px;
  background: cadetblue;
}
.img {
  width: 80%;
  height: 80%;
}
.erweima {
  width: 600px;
  height: 600px;
}
p {
  margin-top: -10;
}
</style>



